@use 'base/mixins' as m;
@use 'base/variables' as v;

.not-webp .contacto {
    @include m.bgOpaco('../img/habilidades.jpg', v.$AzulFuerte);
}
.webp .contacto {
    @include m.bgOpaco('../img/habilidades.webp', v.$AzulFuerte);
}
.contacto {
    min-height: 70rem;
    background-position: center center;
    margin-bottom: -17.5rem;
}

::placeholder {
    font-weight: bold;
    color: rgba(184, 180, 180, 0.925);
    text-align: center;
}

.form {
    position: relative;
    margin: 0 auto;
    padding: 2rem 0 1rem 0;
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 60rem;
    box-shadow: 0 30px 30px -30px rgba(252, 250, 250, 0.925);
    .label {
        font-size: 1.4rem;
        font-family: v.$fuente_parrafos;
        font-weight: 900;
        text-align: justify;
        color: v.$Blanco;
    }
}

.salto {
    color: v.$Licorice;
    text-align: center;
    font-size: 4.2rem;
    font-weight: 900;
    letter-spacing: 1px;
    line-height: 1.1;
    display: block;
    align-items: center;
    justify-content: center;
    &:first-of-type {
        padding-top: 6rem;
    }
}

.form input {
    outline: 0;
    color: v.$Blanco;
    border: 1px solid v.$Gris;
    padding: .8rem 1.4rem;
    width: 100%;
    height: 50px;
    background-color: unset;
    border-radius: 1px;
}

.form textarea {
    height: 100px;
    width: 100%;
    resize: none;
    outline: 0;
    color: v.$Blanco;
    padding: .8rem 1.4rem;
    border: 1px solid v.$Gris;
    background-color: unset;
    border-radius: 1px;
}

.form button {
    align-self: flex-end;
    width: 10rem;
    padding: 1rem;
    outline: 0;
    border: 0;
    border-radius: 1px;
    font-size: 1.8rem;
    font-weight: 700;
    background-color: v.$PaleDogwood;
    color: v.$Blanco;
    transition: all ease-in-out 200ms;
    &:hover {
        cursor: pointer;
        background-color: darken(v.$PaleDogwood, 10);
    }
}
    