html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Noto Sans", sans-serif;
  font-size: 1.6rem;
  padding-top: 8rem;
  color: #ad8b6b;
}

[class$=__contenedor] {
  max-width: 133rem;
  margin: 0 auto;
  width: 90%;
}

.hidden {
  opacity: 0;
  filter: blur(10px);
  transform: translateX(-100%);
  transition: all 600ms ease-in-out;
}

.show {
  opacity: 1;
  filter: blur(0);
  transform: translateX(0);
}

@media (prefers-reduced-motion) {
  .hidden {
    transition: none;
  }
}
img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

a {
  text-decoration: none;
}

p {
  font-size: 1.6rem;
}

.header {
  padding: 1rem 0 0 0;
  position: fixed;
  z-index: 2;
  width: 100%;
  left: 0;
  top: 0;
  background-image: url(../img/top.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 2px 5px #242948;
}
@media (min-width: 768px) {
  .header {
    text-align: justify;
    padding: 1.5rem 0;
  }
}
.header .logo-barras {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header .logo-barras {
    display: none;
  }
}
.header .logo {
  background-image: url(../img/logo.svg);
  width: 3rem;
  height: 3rem;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .header .logo {
    display: none;
  }
}
@media (min-width: 768px) {
  .header__grid {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header a.botonhead {
  color: #ad8b6b;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  appearance: none;
  position: relative;
  padding: 1.2rem 1.7rem;
  border: 0;
  transition: all ease 300ms;
}
.header a.botonhead ::after {
  --m-i: linear-gradient(#000, #000);
  --m-o: content-box, padding-box;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 3px;
  border-radius: 4px;
  background-image: conic-gradient(#242948, #96a4d3, #ad8b6b, #13070c, #242948, #96a4d3, #ad8b6b, #13070c);
  -webkit-mask-image: var(--m-i), var(--m-i);
  mask-image: var(--m-i), var(--m-i);
  -webkit-mask-origin: var(--m-o);
  mask-origin: var(--m-o);
  -webkit-mask-clip: var(--m-o);
  mask-clip: var(--m-o);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
  filter: hue-rotate(0);
  animation: rotate-hue linear 1000ms infinite;
  animation-play-state: running;
}
@keyframes rotate-hue {
  to {
    filter: hue-rotate(1turn);
  }
}
.header a.botonhead:hover {
  transform: scale(1.1);
}
.header .botonhead {
  display: none;
}
@media (min-width: 768px) {
  .header .botonhead {
    display: flex;
  }
}
.header .curriculum {
  background-image: url(../img/download.svg);
  margin-right: 0.5rem;
  width: 2rem;
  height: 2rem;
  background-repeat: no-repeat;
}

.footer {
  background-image: url(../img/bottom.svg);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 4rem 0;
  background-position: center center;
}
.footer__titulo {
  text-align: center;
  margin-top: 2.5rem;
  margin-bottom: 3rem;
  color: #ad8b6b;
  font-weight: 800;
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .footer__titulo {
    margin-top: 0;
  }
}
.footer__redes .redes {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 2rem;
}
.footer__redes .redes__svg img {
  width: 4rem;
}
.footer__redes .redes__icono img {
  transform: translate(0px);
  animation: 4s ease-in-out infinite float;
}
.footer__redes .redes__icono img:hover {
  cursor: pointer;
}

@keyframes float {
  0% {
    transform: translate(0px);
  }
  25% {
    transform: translateY(-13px);
  }
  50% {
    transform: translateY(-8px);
  }
  75% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}
.biografia__grid {
  display: grid;
  grid-template-rows: 1.5fr 1fr;
}
@media (min-width: 768px) {
  .biografia__grid {
    grid-template-rows: unset;
    grid-template-columns: 1.9fr 1fr;
  }
}
.biografia__foto {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-row: 1/2;
  margin-top: 2.5rem;
  z-index: 1;
}
@media (min-width: 768px) {
  .biografia__foto {
    justify-content: flex-end;
    grid-column: 2/3;
    grid-row: 1/2;
    margin-top: 0;
  }
}
.biografia__foto img {
  border-radius: 45% 55% 54% 46%/56% 57% 43% 44%;
  width: 21rem;
  height: 21rem;
  border: 3px solid rgb(150, 164, 211);
  animation: foto 12s ease-in-out infinite;
  -webkit-box-shadow: -11px -8px 44px 4px rgb(150, 164, 211);
  -moz-box-shadow: -11px -8px 44px 4px rgb(150, 164, 211);
  box-shadow: -11px -8px 44px 4px rgb(150, 164, 211);
}

@keyframes foto {
  20% {
    border-radius: 32% 68% 68% 32%/27% 38% 62% 73%;
    -webkit-box-shadow: 19px -8px 44px 4px rgb(150, 164, 211);
    -moz-box-shadow: 19px -8px 44px 4px rgb(150, 164, 211);
    box-shadow: 19px -8px 44px 4px rgb(150, 164, 211);
  }
  40% {
    border-radius: 44% 56% 68% 32%/41% 38% 62% 59%;
    -webkit-box-shadow: 19px 22px 44px 4px rgb(150, 164, 211);
    -moz-box-shadow: 19px 22px 44px 4px rgb(150, 164, 211);
    box-shadow: 19px 22px 44px 4px rgb(150, 164, 211);
  }
  50% {
    border-radius: 57% 43% 54% 46%/56% 26% 74% 44%;
  }
  70% {
    border-radius: 60% 40% 46% 54%/51% 33% 67% 49%;
    -webkit-box-shadow: -9px 22px 44px 4px rgb(150, 164, 211);
    -moz-box-shadow: -9px 22px 44px 4px rgb(150, 164, 211);
    box-shadow: -9px 22px 44px 4px rgb(150, 164, 211);
  }
  80% {
    border-radius: 51% 49% 43% 57%/57% 67% 33% 43%;
  }
  100% {
    border-radius: 32% 68% 57% 43%/27% 26% 74% 73%;
    -webkit-box-shadow: -11px -8px 44px 4px rgb(150, 164, 211);
    -moz-box-shadow: -11px -8px 44px 4px rgb(150, 164, 211);
    box-shadow: -11px -8px 44px 4px rgb(150, 164, 211);
  }
}
