@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    display: flex;
    flex-direction: column;
    text-align: center;
    a {
        font-family: v.$fuente_parrafos;
        font-size: .5rem;
        color: v.$Licorice;
        @include m.tablet { 
            border: none;
        }   
    }
    @include m.tablet {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        margin: 0;
        a {
            font-size: 2rem;
            margin-right: 1.2rem;
            font-weight: 600;
            @include m.desktop { 
                font-size: 2rem;
                margin-right: 1.5rem;
            }   
        }
    }
    &__enlaces {
        color: v.$Negro;
        transition: all .8s ease;
        font-weight: 700;
    }
    &__enlaces span {
        display: none;
        @include m.tablet { 
            display: inline-block;
            margin-right: 1.2rem;
            font-size: 2.5rem;
            font-weight: 400;
        }   
        @include m.desktop { 
            margin-right: 2rem;
        }   
    }
    &__enlaces:hover {
        @include m.tablet { 
            transform: scale(1.1);
        }
    }
}