@use 'base/variables' as v;
@use 'base/mixins' as m;

.mobile-menu {
    background-image: url(../img/barras.svg);
    background-repeat: no-repeat;
    background-position: center center;
    transition: all ease-in-out 300ms;
    width: 3rem;
    height: 3rem;
    &.mostrar {
        background-image: url(../img/close.svg);
        transition: all ease-in-out 300ms;
    }
    @include m.tablet { 
        display: none;
    }   
    &:hover {
        cursor: pointer;
    }
}
.navegacion {
    visibility: hidden;
    @include m.tablet { 
        visibility: visible;
    }   
    &.mostrar {
        visibility: visible;
        background-color: v.$Blanco;
        margin-top: 1rem;
        @include m.tablet { 
            margin-top: 0;
        }   
    }
    &.mostrar a {
        color: v.$Licorice;
        font-size: 1.5rem;
        text-transform: uppercase;
        padding: .5rem 1rem;
        box-shadow: 0px 1px 2px v.$PaleDogwood;
        border-left: 2px solid v.$PaleDogwood;
        border-right: 2px solid v.$PaleDogwood;
        margin-bottom: .5rem;
        @include m.tablet { 
            font-size: 2rem;
            text-transform: none;
            padding: unset;
            border: none;
            box-shadow: none;
            background-color: unset;
            margin-top: 0;
            margin-bottom: 0;
        }   
    }
    &.mostrar a:last-of-type {
        margin-bottom: 1rem;
        @include m.tablet { 
            margin-bottom: 0;
        }   
    }
    &.mostrar a:hover {
        @include m.tablet { 
            transform: scale(1.1);
        }
    }
}