@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    padding-top: 8rem;
    color: v.$Licorice;
}
[class$="__contenedor"] {
    max-width: 133rem;
    margin: 0 auto;
    width: 90%;
}
.hidden {
    opacity: 0;
    filter: blur(10px);
    transform: translateX(-100%);
    transition: all 600ms ease-in-out;
}
.show {
    opacity: 1;
    filter: blur(0);
    transform: translateX(0);
}
@media(prefers-reduced-motion) {
    .hidden {
        transition: none;
    }
}

img {
    max-width: 100%;
    display: block;
}
ul {
    padding: 0;
    list-style: none;
    margin: 0;
}
h1, h2, h3 {
    margin: 0;
    line-height: 1.2;

}
h1, h2 {
    font-weight: 400;
}
h1 {
    font-size: 4.4rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 2.4rem;
    font-weight: 300;
}
a {
    text-decoration: none;
}
p {
    font-size: 1.6rem;
}