@use 'base/mixins' as m;
@use 'base/variables' as v;

.biografia {
    &__grid {
        display: grid;
        grid-template-rows: 1.5fr 1fr;
        @include m.tablet { 
            grid-template-rows: unset;
            grid-template-columns: 1.9fr 1fr;
        }    
    }
    &__foto {
        display: flex;
        justify-content: center;
        align-items: center;
        grid-row: 1 / 2;
        margin-top: 2.5rem;
        z-index: 1;
        @include m.tablet { 
            justify-content: flex-end;
            grid-column: 2 / 3;
            grid-row: 1 / 2;
            margin-top: 0;
        }
    }
    &__foto img {
        border-radius: 45% 55% 54% 46% / 56% 57% 43% 44% ;
        width: 21rem;
        height: 21rem;
        border: 3px solid v.$PaleDogwood;  
        animation: foto 12s ease-in-out infinite;
        -webkit-box-shadow: -11px -8px 44px 4px rgba(150, 164, 211, 1);
        -moz-box-shadow: -11px -8px 44px 4px rgba(150, 164, 211, 1);
        box-shadow: -11px -8px 44px 4px rgba(150, 164, 211, 1);
    }
}
@keyframes foto {
    20% {
        border-radius: 32% 68% 68% 32% / 27% 38% 62% 73% ;
        -webkit-box-shadow: 19px -8px 44px 4px rgba(150, 164, 211, 1);
        -moz-box-shadow: 19px -8px 44px 4px rgba(150, 164, 211, 1);
        box-shadow: 19px -8px 44px 4px rgba(150, 164, 211, 1);
    }
    40% {
        border-radius: 44% 56% 68% 32% / 41% 38% 62% 59% ;
        -webkit-box-shadow: 19px 22px 44px 4px rgba(150, 164, 211, 1);
        -moz-box-shadow: 19px 22px 44px 4px rgba(150, 164, 211, 1);
        box-shadow: 19px 22px 44px 4px rgba(150, 164, 211, 1);
    }
    50% {
        border-radius: 57% 43% 54% 46% / 56% 26% 74% 44%  ;
    }
    70% {
        border-radius: 60% 40% 46% 54% / 51% 33% 67% 49% ;
        -webkit-box-shadow: -9px 22px 44px 4px rgba(150, 164, 211, 1);
        -moz-box-shadow: -9px 22px 44px 4px rgba(150, 164, 211, 1);
        box-shadow: -9px 22px 44px 4px rgba(150, 164, 211, 1);
    }
    80% {
        border-radius: 51% 49% 43% 57% / 57% 67% 33% 43% ;
    }
    100% {
        border-radius: 32% 68% 57% 43% / 27% 26% 74% 73%;
        -webkit-box-shadow: -11px -8px 44px 4px rgba(150, 164, 211, 1);
        -moz-box-shadow: -11px -8px 44px 4px rgba(150, 164, 211, 1);
        box-shadow: -11px -8px 44px 4px rgba(150, 164, 211, 1);
    }
}
.info {
    a.botonheadin {
        display: flex;
        align-items: center;
        margin: 0 auto;
        margin-top: -2rem;
        margin-bottom: 2rem;
        width: 18rem;
        color: v.$Negro;
        font-weight: 700;
        position: relative;
        padding: 1.2rem 1.7rem;
        border: 0;
        transition: all ease 300ms;
        ::after {
            --m-i: linear-gradient(#000, #000);
            --m-o: content-box, padding-box;
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            padding: 3px;
            border-radius: 4px;
            background-image: conic-gradient(
                    #242948,
                    #96a4d3,
                    #ad8b6b,
                    #13070c,
                    #242948,
                    #96a4d3,
                    #ad8b6b,
                    #13070c,
                );
            -webkit-mask-image: var(--m-i), var(--m-i);
            mask-image: var(--m-i), var(--m-i);
            -webkit-mask-origin: var(--m-o);
            mask-origin: var(--m-o);
            -webkit-mask-clip: var(--m-o);
            mask-clip: var(--m-o);
            -webkit-mask-composite: destination-out;
            mask-composite: exclude;
            filter: hue-rotate(0);
            animation: rotate-hues linear 800ms infinite;
            animation-play-state: running;
        }
        @keyframes rotate-hues {
            to {
             filter: hue-rotate(1turn);
            }
        }
        @include m.tablet { 
            display: none;
        }
    }
    a.botonheadin:hover {
        transform: scale(1.1);
    }
    .curriculumin {
        background-image: url(../img/download.svg);
        margin-right: .5rem;
        width: 2rem;
        height: 2rem;
        background-repeat: no-repeat;
    }

    .sobre {
        @include m.tablet { 
            margin: 0 auto;
        } 
        &__texto {
            text-align: justify;
            font-size: 2rem;
            font-family: v.$fuente_parrafos;
            font-weight: 500;
            &:last-of-type {
                margin: 0 0 4rem 0;
            }
            span {
                font-size: 2.5rem;
                margin-top: 1.5rem;
                text-align: center;
                display: block;
                font-weight: 900;
                color: v.$principal;
            }
            @include m.tablet { 
                text-align: start;
            }
        } 
    }
    .cargo {
        text-align: center;
        padding: 0 0 2rem 0;  
        h1 {
            display: inline-block;
            color: #000;
            background: v.$Blanco;
            font-family: v.$fuente_secundaria;
            mix-blend-mode: multiply;
            position: relative;
            font-size: 3rem;
            font-weight: 500;
            @include m.tablet { 
                font-size: 5rem;
                font-weight: 700;
            }   
        }
        h1:before {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            background: linear-gradient(to right, v.$principal, v.$PaleDogwood);
            mix-blend-mode: screen;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
}
.flex {
    display: flex;
    flex-direction: column-reverse;
    @include m.tablet { 
        flex-direction: row;
        align-items: center;
    }   
    &__icono {
        display: unset;
        @include m.tablet { 
            display: block;
            background-image: url('../img/Recurso 16.svg');
            width: 20rem;
            height: 25em;
            background-repeat: no-repeat;
            background-position: center center;
        }
    }
    &__info {
        padding: 3rem 0 0 0;
        @include m.desktop { 
            padding: 3rem 0 2rem 0;
        }
    }

    &__info p {
        @include m.headLineaAbajo;
        color: v.$principal;
        font-family: v.$fuente_principal;
        font-weight: 800;
        margin: 0;
        font-size: 2rem;
        line-height: 1.1;
        @include m.tablet { 
            font-size: 3rem;
            margin-left: 3.5rem;
        }  
    }
    &__info p span {
        font-family: v.$fuente_parrafos;
        color: v.$Licorice;
        margin-left: .25rem;
        font-weight: 500;
        font-size: 1.4rem;
        display: block;
    }
}

#particles-js {
