@use 'base/mixins' as m;
@use 'base/variables' as v;

.proyectos {
    margin-bottom: 2rem;
    &__svg {
        display: flex;
        justify-content: center;
        padding: 4rem 0;
    }
    &__titulo {
        font-family: v.$fuente_principal;
        color: v.$principal;
        text-align: center;
        font-size: 4.2rem;
        font-weight: 900;
        margin-bottom: 6rem;
    }
    &__grid {
        display: grid;
        width: auto;
        @include m.tablet { 
            @include m.grid(2, 2rem);
        } 
        @include m.desktop { 
            @include m.grid(3, 2rem);
        } 
    }
}
.proyecto {
    margin-bottom: 5rem;
    padding: 2rem 1rem;
    background-color: v.$principal;
    border-radius: 5px;
    transition: all ease-in-out 500ms;
    &__info {
        min-height: 45rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__nombre {
        font-family: v.$fuente_principal;
        color: v.$PaleDogwood;
        text-align: center;
        font-size: 3rem;
        font-weight: 900;
        margin: 2rem 0;
    }
    &__descripcion {
        font-family: v.$fuente_parrafos;
        font-weight: 600;
        span {
            font-family: v.$fuente_principal;
            text-align: center;
            display: block;
            margin-top: 2rem;
            font-weight: 800;
            font-size: 2rem;
            color: v.$PaleDogwood;
        }
    }
    &__lista {
        .nombre {
            font-weight: 900;
            margin: 0;
        }
    }
    &__boton {
        background-color: v.$PaleDogwood;
        font-size: 2rem;
        padding: .5rem 3rem;
        border-radius: 1rem;
        font-weight: 700;
        display: block;
        margin: 0 auto;
        border-radius: 0;
        width: 100%;
        text-align: center;
        color: v.$Blanco;
        text-decoration: none;
        transition: all .2s ease;
        border: none;
        &:hover {
            cursor: pointer;
            background-color: darken(v.$PaleDogwood, 10);
        }
        @include m.tablet {
            margin-top: auto;
            width: 20rem;
        }   
    }
}


//TODO: Galeria 
.img-gallery img{
    width: 100%;
    height: 25rem;
    object-fit: cover;
    object-position: top;
    cursor: pointer;
    transition: 800ms;
}
.img-gallery img:hover{
    transform: translate(0px);
    animation: float 1.8s ease-in-out infinite;
}
@keyframes float2 {
    0% {
        transform: translate(0px);
    }
    50% {
        transform: translateY(-13px);
    }
    100% {
        transform: translateY(0px);
    }
}

//TODO: Full Img
.img-full {
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.8);
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    align-items: center;
    justify-content: center;
    z-index: 4;
}
.img-full span {
    position: absolute;
    top: 3%;
    right: 5%;
    font-size: 3rem;
    font-weight: 700;
    color: v.$Blanco;
    cursor: pointer;
    @include m.tablet { 
        font-size: 4rem;
    }   
}
.img-full img {
    transition: 2s;
    width: 95%;
    height: 40%;
    max-width: 60rem;
    @include m.tablet { 
        min-width: 85%;
        height: 70%;
    }   
}


//TODO: Estilos SVG
.three-body {
    --uib-size: 45px;
    --uib-speed: 0.8s;
    --uib-color: #5D3FD3;
    position: relative;
    display: inline-block;
    height: var(--uib-size);
    width: var(--uib-size);
    animation: spin78236 calc(var(--uib-speed) * 2.5) infinite linear;
}
   
.three-body__dot {
    position: absolute;
    height: 100%;
    width: 30%;
}

.three-body__dot:after {
    content: '';
    position: absolute;
    height: 0%;
    width: 100%;
    padding-bottom: 100%;
    background-color: var(--uib-color);
    border-radius: 50%;
}

.three-body__dot:nth-child(1) {
    bottom: 5%;
    left: 0;
    transform: rotate(60deg);
    transform-origin: 50% 85%;
}

.three-body__dot:nth-child(1)::after {
    bottom: 0;
    left: 0;
    animation: wobble1 var(--uib-speed) infinite ease-in-out;
    animation-delay: calc(var(--uib-speed) * -0.3);
}

.three-body__dot:nth-child(2) {
    bottom: 5%;
    right: 0;
    transform: rotate(-60deg);
    transform-origin: 50% 85%;
}

.three-body__dot:nth-child(2)::after {
    bottom: 0;
    left: 0;
    animation: wobble1 var(--uib-speed) infinite
    calc(var(--uib-speed) * -0.15) ease-in-out;
}

.three-body__dot:nth-child(3) {
    bottom: -5%;
    left: 0;
    transform: translateX(116.666%);
}

.three-body__dot:nth-child(3)::after {
    top: 0;
    left: 0;
    animation: wobble2 var(--uib-speed) infinite ease-in-out;
}

@keyframes spin78236 {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes wobble1 {
    0%,
    100% {
        transform: translateY(0%) scale(1);
        opacity: 1;
    }

    50% {
        transform: translateY(-66%) scale(0.65);
        opacity: 0.8;
    }
}

@keyframes wobble2 {
    0%,
    100% {
    transform: translateY(0%) scale(1);
    opacity: 1;
    }

    50% {
        transform: translateY(66%) scale(0.65);
        opacity: 0.8;
    }
}