@use 'base/mixins' as m;
@use 'base/variables' as v;

.certificados {
    padding: 0 0 5rem 0;
    margin-top: -40rem;
    margin-bottom: 3rem;
    @include m.tablet { 
        margin-top: -40rem;
    }   
    @include m.desktop { 
        margin-top: -40rem;
    }
    &__titulo {
        font-family: v.$fuente_principal;
        color: v.$principal;
        text-align: center;
        font-size: 4.2rem;
        font-weight: 800;
        margin-bottom: 2rem;
    }
    .slider {
        height: auto;
        width: auto;
        overflow: hidden;
        align-items: center;
    }
    .slider .slide-track {
        display: flex;
        -webkit-animation: scroll 25s linear infinite;
        animation: scroll 25s linear infinite;
        width: calc(280px * 16);
    }
    .slider .slide {
        display: flex;
        padding: 0 2rem;
    }
    .slider .slide img {
        height: 90%;
        padding: 5px;
        background-color: v.$principal;
        cursor: pointer;
        margin: 2rem;
        border-top-left-radius: 2rem;
        border-bottom-right-radius: 2rem;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
        rgba(0, 0, 0, 0.3) 6px 1px 3px -1px;
    }
    @keyframes scroll {
        0% {
            -webkit-transform: translateX(0);
            transform: translateX(0);
        }
        100% {
            -webkit-transform: translateX(calc(-280px * 8));
            transform: translateX(calc(-280px * 8));
        }
    }




    //TODO: LOADER
    
    .loader-center {
        display: flex;
        justify-content: center;
        margin-bottom: 5rem;
    }
    .loader {
        --path: #2f3545;
        --dot: #5628ee;
        --duration: 3s;
        width: 44px;
        height: 44px;
        position: relative;
    }
    .loader:before {
        content: '';
        width: 6px;
        height: 6px;
        border-radius: 50%;
        position: absolute;
        display: block;
        background: var(--dot);
        top: 37px;
        left: 19px;
        transform: translate(-18px, -18px);
        animation: dotRect var(--duration) cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite;
    }
    .loader svg {
        display: block;
        width: 100%;
        height: 100%;
    }
    .loader svg rect, .loader svg polygon, .loader svg circle {
        fill: none;
        stroke: var(--path);
        stroke-width: 10px;
        stroke-linejoin: round;
        stroke-linecap: round;
    }
    .loader svg polygon {
        stroke-dasharray: 145 76 145 76;
        stroke-dashoffset: 0;
        animation: pathTriangle var(--duration) cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite;
    }
    .loader svg rect {
        stroke-dasharray: 192 64 192 64;
        stroke-dashoffset: 0;
        animation: pathRect 3s cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite;
    }
    .loader svg circle {
        stroke-dasharray: 150 50 150 50;
        stroke-dashoffset: 75;
        animation: pathCircle var(--duration) cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite;
    }
    .loader.triangle {
        width: 48px;
    }
    .loader.triangle:before {
        left: 21px;
        transform: translate(-10px, -18px);
        animation: dotTriangle var(--duration) cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite;
    }
    @keyframes pathTriangle {
        33% {
            stroke-dashoffset: 74;
        }
        66% {
            stroke-dashoffset: 147;
        }
        100% {
            stroke-dashoffset: 221;
        }
    }
    @keyframes dotTriangle {
        33% {
            transform: translate(0, 0);
        }
        66% {
            transform: translate(10px, -18px);
        }
        100% {
            transform: translate(-10px, -18px);
        }
    }
    
    @keyframes pathRect {
        25% {
            stroke-dashoffset: 64;
        }
        50% {
            stroke-dashoffset: 128;
        }     
        75% {
            stroke-dashoffset: 192;
        }
        100% {
            stroke-dashoffset: 256;
        }
    }
    
    @keyframes dotRect {
        25% {
            transform: translate(0, 0);
        }
        50% {
            transform: translate(18px, -18px);
        }
        75% {
            transform: translate(0, -36px);
        }
        100% {
            transform: translate(-18px, -18px);
        }
    }
    
    @keyframes pathCircle {
        25% {
            stroke-dashoffset: 125;
        }
        50% {
            stroke-dashoffset: 175;
        }
        75% {
            stroke-dashoffset: 225;
        }
        100% {
            stroke-dashoffset: 275;
        }
    }
    .loader {
        display: inline-block;
        margin: 0 16px;
    }
}


//TODO: Galeria 
.img-gallery2 img{
    width: 23rem;
    height: 40rem;
    cursor: pointer;
    transition: all ease-in-out 600ms;
}
.img-gallery2 img:hover{
    transform: scale(0.9); 
}
.img-full2 {
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.8);
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    align-items: center;
    justify-content: center;
    z-index: 4;
}
.img-full2 span {
    position: absolute;
    top: 3%;
    right: 5%;
    font-size: 3rem;
    font-weight: 700;
    color: v.$Blanco;
    cursor: pointer;
    @include m.tablet { 
        font-size: 4rem;
    }   
}
.img-full2 img {
    width: 90%;
    height: 35%;
    max-width: auto;
    @include m.tablet { 
        width: 95%;
        height: 55%;
    }   
    @include m.desktop { 
        width: 77%;
        height: 83%;
    }
}