@use 'variables' as v; 

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin mqmodified {
    @media (min-width: 280px) {
        @content;
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
@mixin grid1-2($espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: 1fr 2fr;
}
@mixin grid2-1($espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: 2fr 1fr;
}

@mixin bgOpaco($url, $degradado) {
    background-image: linear-gradient($degradado 0%, $degradado 100%), url($url);
    background-repeat: no-repeat;
    background-size: cover;
}

@mixin scroll {
    overflow-y: hidden;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;

    > * {
        scroll-snap-align: center;
    }
}

@mixin boton($color, $texto) {
    background-color: $color;
    font-size: 2rem;
    padding: .5rem 3rem;
    border-radius: 1rem;
    font-weight: 700;
    display: block;
    width: 100%;
    text-align: center;
    color: $texto;
    text-decoration: none;
    transition: all .2s ease;
    border: none;
    &:hover {
        cursor: pointer;
        background-color: darken($color, 10);
    }
    @include tablet {
        width: auto;
    }
}

@mixin headLineaAtras($color) {
    display: inline-block;
    &::after {
        content: '';
        background-color: $color;
        height: 1rem;
        width: calc(100% + 1rem);
        display: block;
        margin: -1.4rem 0 0 0;
    } 
}

@mixin headLineaAbajo {
    &::after {
        content: '';
        display: block;
        height: 1.2rem;
        width: 15rem;
        margin-top: .2rem;
        background-image: linear-gradient(to right, v.$Verde 0%, v.$Verde 33.3%,
        lighten(v.$Blanco, 20%) 33.3%, lighten(v.$Blanco, 20%) 66.6%, lighten(v.$Rojo, 20%) 66.6%, lighten(v.$Rojo, 20%) 100%);
    }
}

@mixin headCursiva($color, $colors) {
    font-family: v.$cursiva;
    font-size: 3.5rem;
    color: $color;
    line-height: 1;
    span {
        display: block;
        font-size: 2.8rem;
        font-family: v.$fuente_secundaria;
        text-align: center;
        color: $colors;
    }
}