@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding: 1rem 0 0 0;
    position: fixed;
    z-index: 2;
    width: 100%;
    left: 0;
    top: 0;
    background-image: url(../img/top.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    background-color: v.$Blanco;
    box-shadow: 0px 2px 5px v.$principal;
    ;
    @include m.tablet { 
        text-align: justify;
        padding: 1.5rem 0;
    }
    .logo-barras {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        @include m.tablet { 
            display: none;
        }   
    }
    .logo {
        background-image: url(../img/logo.svg);
        width: 3rem;
        height: 3rem;
        background-repeat: no-repeat;
        background-position: center;
        @include m.tablet { 
            display: none;
        }
    }
    &__grid {
        @include m.tablet { 
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }
    a.botonhead {
        color: v.$Licorice;
        font-family: v.$fuente_parrafos;
        font-weight: 700;
        appearance: none;
        position: relative;
        padding: 1.2rem 1.7rem;
        border: 0;
        transition: all ease 300ms;
        ::after {
            --m-i: linear-gradient(#000, #000);
            --m-o: content-box, padding-box;
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            padding: 3px;
            border-radius: 4px;
            background-image: conic-gradient(
                   #242948,
                   #96a4d3,
                   #ad8b6b,
                   #13070c,
                   #242948,
                   #96a4d3,
                   #ad8b6b,
                   #13070c,
               );
            -webkit-mask-image: var(--m-i), var(--m-i);
            mask-image: var(--m-i), var(--m-i);
            -webkit-mask-origin: var(--m-o);
            mask-origin: var(--m-o);
            -webkit-mask-clip: var(--m-o);
            mask-clip: var(--m-o);
            -webkit-mask-composite: destination-out;
            mask-composite: exclude;
            filter: hue-rotate(0);
            animation: rotate-hue linear 1000ms infinite;
            animation-play-state: running;
        }
        @keyframes rotate-hue {
            to {
             filter: hue-rotate(1turn);
            }
        }
    }
    a.botonhead:hover {
        transform: scale(1.1);
    }
    .botonhead {
        display: none;
        @include m.tablet { 
            display: flex;
        }   
    }
    .curriculum {
        background-image: url(../img/download.svg);
        margin-right: .5rem;
        width: 2rem;
        height: 2rem;
        background-repeat: no-repeat;
    }
}