@use 'base/variables' as v;
@use 'base/mixins' as m;

.not-webp .habilidades {
    @include m.bgOpaco('../img/habilidades.jpg', v.$OpacoNegro);
}
.webp .habilidades {
    @include m.bgOpaco('../img/habilidades.webp', v.$OpacoNegro);
}
.habilidades {
    padding: 2rem 0;
    min-height: 80rem;
    background-position: center center;
    &__svg {
        display: flex;
        justify-content: center;
    }
    &__titulo {
        font-family: v.$fuente_principal;
        text-align: center;
        font-size: 4.2rem;
        font-weight: 900;
        color: v.$Licorice;
        margin-bottom: 4rem;
    }
    &__lista {
        grid-row: unset; 
        display: flex;
        flex-direction: column;
        @include m.tablet { 
            align-items: normal;
        }   
        li {
            box-shadow: 0px 7px 8px rgb(255, 255, 255);
            width: 100%;
            margin-bottom: 6rem;
            @include m.tablet { 
                margin-bottom: 3rem;
            }   
        }
        @include m.tablet { 
            @include m.grid(2, 3rem);   
        }
        @include m.desktop { 
            @include m.grid(4, 2rem);  
            li {     
                &:nth-child(5) {
                    grid-area: 3 / 3;
                }
                &:nth-child(9) {
                    grid-area: 3 / 2;
                }
            }
        }
    }
}
.lista {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__nombre {
        @include m.headLineaAtras(v.$Licorice);
        color: v.$Blanco;
        font-family: v.$fuente_principal;
        font-weight: 800;
        margin-top: 6rem;
        margin-bottom: 3rem;
    }
    img {
        height: 100%;
        transform: translate(0px);
        animation: float 1.8s ease-in-out infinite;
        &:nth-child(4) {
            margin-top: 1rem;
        }
    }
    &__icono {
        width: 7rem;
        margin-bottom: 2rem;
        min-height: 3.5rem;
    }
    .barra {
        height: 2rem;
        @include m.tablet { 
            height: 4rem;
        }
    }
    .barra-titulo {
        margin: 0 0 2rem 0;
        color: v.$Licorice;
        font-family: v.$fuente_parrafos;
        font-weight: 800;
    }
    progress {
        border-radius: 5px;
        margin: 1rem 0 .5rem 0;
        width: 70%;
        height: 20%;
        border: none;
    }
    progress::-webkit-progress-bar {
        background-color: v.$Blanco;
        border-radius: 5px;
    }
    progress::-webkit-progress-value {
        background-color: v.$PaleDogwood;
        border-radius: 5px;
    }
}
@keyframes float {
    0% {
        transform: translate(0px);
    }
    50% {
        transform: translateY(-13px);
    }
    100% {
        transform: translateY(0px);
    }
}
.vector {
    height: 7.5rem;
}


